import { useEffect, useContext } from "react";
import { socket } from '../context/SocketContext';
import { BotsContext } from '../context/BotsContext';
import { ServerContext } from '../context/ServerContext';
import { ConfigurationContext } from '../context/ConfigurationContext';

export default function SocketService() {
    const [{ status, servertime }, setServer] = useContext(ServerContext);
    const [bots, setBots] = useContext(BotsContext);
    const [configuration, setConfiguration] = useContext(ConfigurationContext);

    useEffect(() => {
        socket.on("status", (server) => {
            setServer({
                status: server.status,
                serverversion: server.serverversion,
                servertime: server.servertime,
                access: server.access,
                externalip: server.externalip,
                publicKey: server.publicKey,
                loginNow: server.loginNow,
            });
        });
        socket.on("bots", (bots) => {
            setBots(bots);
        });
        socket.on("settings", (config) => {
            setConfiguration(config);
        });
        socket.on("disconnect", data => {
            setServer({
                status: 'disconnected',
            });
        });

        return () => {
            socket.disconnect();
        }
    }, []);

    return (null);
}